/* Item */
.item {
    height: 260px;
    width: 260px;
    border-radius: 10px;
    background-color: #FFFFFF;
    display: grid;
    grid-template-rows: 65% 35%;
    row-gap: 0;
}
.itemImg {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    overflow: hidden;
    border-radius: 10px 10px 0 0;
}

.itemImg img {
    height: 100%;
    border-radius: 10px 10px 0 0;
}

.itemDesp {
    padding: 10px 10px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.videoTitle {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
    font-size: 16px;
    line-height: 20px;
    color: #444444;
    margin-top: 5px;
}

.videoAuthor {
    text-align: end;
    color: #BBBBBB;
    font-size: 14px;
    line-height: 20px;
}

.videoAuthor span {
    margin-right: 10px;
}